import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function epsActual(tableName: string) {
  const title = "Actual EPS"
  return genericColumn({
    endpoint: "companyLatestSurprise",
    columnKeys: ["epsActual"],
    size: 120,
    title,
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip="Explain the Actual EPS"
          queryResult={info.row.original.financialData.companyLatestSurprise}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about EPS?`,
              value: info.row.original.financialData.companyLatestSurprise?.data?.epsActual,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyLatestSurprise?.data?.epsActual}
        />
      )
    },
  })
}

export { epsActual }
